.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas:
  "header header"
  "nav content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2.5fr 9.5fr;
}

.app-wrapper-content {
    grid-area: content;
    background-color: lightblue;
}
